
$(document).ready(function() {
    function dateFormat(d) {
        var date = new Date(d);
        return ((date.getDate() + "").padStart(2, "0") 
           + "/" + (date.getMonth() + 1) + "").padStart(2, "0") 
           + "/" + date.getFullYear();
     }
    function loadOpciones() {
        $(".loader").show();
        var parametro = $("#parametro").val();
        console.log(parametro + 'Parametro id');
        $("#tabla tbody").empty();
        if ($.trim(parametro) != "") {
            console.log(parametro + 'Entra al if');
            $.get("/parametrizacion/opciones/" + parametro, function(response, state) {
                console.log(parametro + 'Hace el get');
                $.each(response, function(index, value) {
                    $('#tabla tbody:last-child').append(
                        '<tr></tr>'
                        );
                        $('#tabla tr:last').append(
                        '<td>'+ value.id +'</td>'+
                        '<td>'+ value.nombre +'</td>'+
                        '<td>'+ value.orden +'</td>'+
                        '<td>'+ (value.active == 1 ? "Activo":"Inactivo") +'</td>'+
                        '<td>'+ value.creator_user.name +'</td>'+
                        '<td>'+ dateFormat(value.created_at) +'</td>'+
                        '<td>'+ value.updater_user.name +'</td>'+
                        '<td>'+ dateFormat(value.updated_at) +'</td>'+
                        '<td><a href="/parametrizacion/' + value.id + '/edit" class="btn btn-sm btn-secondary" title="Editar usuario"><i class="fa fa-pen"></i></a></td>');
                });
            });
        }
        $(".loader").fadeOut("slow");
    }
    loadOpciones();
    $("#parametro").on("change", loadOpciones);
});
